import L from "leaflet";

(L.Icon.Pulse = L.DivIcon.extend({
  options: { className: "", iconSize: [12, 12], color: "red" },
  initialize: function(e) {
    L.setOptions(this, e);
    var t =
      "lpi-" + new Date().getTime() + "-" + Math.round(1e5 * Math.random());
    this.options.className =
      this.options.className + " leaflet-pulsing-icon " + t;
    var o = "." + t + "{background-color:" + this.options.color + ";}";
    o +=
      "." + t + ":after{box-shadow: 0 0 6px 2px " + this.options.color + ";}";
    var n = document.createElement("style");
    n.styleSheet
      ? (n.styleSheet.cssText = o)
      : n.appendChild(document.createTextNode(o)),
      document.getElementsByTagName("head")[0].appendChild(n),
      L.DivIcon.prototype.initialize.call(this, e);
  },
})),
  (L.icon.pulse = function(e) {
    return new L.Icon.Pulse(e);
  });
